@import "variables.scss";

.price-calc {
  max-width: 1920px;
  background-color: $color-pink;

  &-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    max-width: 1170px;
    margin: 0 auto;
    padding: 60px 0;

    @media (max-width: 1170px) {
      padding-left: 10px;
      padding-right: 10px;
      overflow: hidden;
    }
  }

  &__text {
    color: #ffffff;
    font-size: 20px;
    font-weight: 400;
  }

  &__form {
    margin-top: 25px;

    form {
      display: flex;
      flex-direction: column;
    }
  }

  &__fields {
    display: flex;
  }

  &__phone {
    margin-left: 60px;
  }

  &__name-field, &__phone-field {
    border: none;
    border-radius: 3px;
    max-width: 345px;
    padding: 14px 24px;
    font-size: 16px;
  }
}